<template>
  <div class="index">
    <Navbar></Navbar>
    <div class="indexMain">
      <div class="indexMainHeader">
        <div class="projectInfo">
          {{ $t("index.text") }}
        </div>
        <div class="getPoints jcsb">
          <div class="getPointsItem" @click="$router.push('/getPoints')">
            <img src="../../assets/img/pic17.png" class="pic17" alt="" />
            <img src="../../assets/img/pic1.png" class="left" alt="" />
            <div class="right">
              <div class="text">{{ $t("index.text1") }}</div>
              <div class="tips">{{ $t("index.text2") }}</div>
            </div>
          </div>
          <div class="getPointsItem" @click="merchantSettlement">
            <img src="../../assets/img/pic18.png" class="pic17" alt="" />
            <img src="../../assets/img/pic2.png" class="left" alt="" />
            <div class="right">
              <div class="text">{{ $t("index.text3") }}</div>
              <div class="tips" v-if="userInfo.identity == 1">
                {{ $t("index.text4") }}
              </div>
              <div class="tips" v-else>{{ $t("index.text5") }}</div>
            </div>
          </div>
        </div>
      </div>

      <div class="indexMainContent">
        <div class="jcsb" style="margin-bottom: 2rem">
          <van-search
            v-model="products"
            shape="round"
            :placeholder="$t('index.text6')"
          />
          <van-button
            class="searchBtn"
            round
            size="small"
            type="info"
            @click="getProductList"
          >
            {{ $t("index.text7") }}</van-button
          >
        </div>

        <div class="productsList" v-if="productList.length > 0">
          <div
            class="productsItem"
            v-for="item in productList"
            :key="item.id"
            @click="
              $router.push({
                path: '/productDetails',
                query: {
                  productInfo: JSON.stringify(item)
                }
              })
            "
          >
            <img :src="item.full_url" alt="" class="img" />

            <div class="priceBox">
              <div class="info">{{ item.title }}</div>
              <div class="price">{{ item.price }}HF</div>
            </div>
          </div>
        </div>

        <!-- 搜索提示 -->
        <van-empty image="search" v-else :description="$t('index.text8')" />
      </div>
    </div>

    <van-popup v-model="applyingMerchantsPopup" class="applyingMerchantsPopup">
      <div class="content">
        <img src="../../assets/img/pic6.png" class="pic6" alt="" />
        <img src="../../assets/img/pic7.png" class="pic7" alt="" />
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="(applyingMerchantsPopup = false), (merchantName = '')"
        />
        <div class="title">{{ $t("index.text3") }}</div>

        <input
          class="merchantName"
          type="text"
          maxlength="8"
          :placeholder="$t('index.text9')"
          v-model="merchantName"
        />
        <div class="confirm mt2r jcc" @click="applyingMerchants">
          {{ $t("index.text10") }}
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      babyLongBalance: 0,
      products: "",
      applyingMerchantsPopup: false,
      merchantName: "",
      productList: [],
      userInfo: {}
    };
  },

  async activated() {
    // this.getProductList();
    // this.getUserInfo();
  },

  methods: {
    // 获取商品列表
    getProductList() {
      this.$api
        .post("product/all", {
          page: 1,
          limit: 999999,
          title: this.products
        })
        .then(res => {
          this.productList = res.data.list;
        });
    },
    async applyingMerchants() {
      if (!this.merchantName) {
        this.$toast(this.$t("index.text9"));
        return;
      }
      this.$toast.loading({
        duration: 0,
        message: "loading...",
        forbidClick: true,
        className: "loading"
      });

      try {
        let gasPrice = await this.getGasPrice();

        let gas = await this.core
          .coreContract()
          .registerMerchant(this.userAddress)
          .estimateGas();
        await this.core
          .coreContract()
          .registerMerchant(this.userAddress)
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });

        this.$api
          .post("user/open_shop", {
            shop_name: this.merchantName
          })
          .then(res => {
            this.$bus.$emit("openShop");
            this.getUserInfo();
            setTimeout(() => {
              this.$toast.clear();
              this.$toast(this.$t("index.text11"));
              this.applyingMerchantsPopup = false;
            }, 1500);
          });
      } catch (error) {
        this.$toast.clear();
      }
    },
    async getUserInfo() {
      this.$api.get("user/info", {}).then(res => {
        this.userInfo = res.data;
      });
    },
    merchantSettlement() {
      if (this.userInfo.identity == 1) return;
      this.applyingMerchantsPopup = true;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.index {
  height: 100%;
  overflow: scroll;
}
.indexMainHeader {
  height: 46.9rem;
  background: url("../../assets/img/headerBg.png") no-repeat;
  background-size: 100% 100%;
  position: relative;
  padding: 1.6rem 1.6rem 4rem 1.6rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  .projectInfo {
    font-weight: bold;
    line-height: 2.8rem;
    color: #fff;
  }

  .getPoints {
    margin-top: 1.6rem;
    .getPointsItem {
      padding: 1.7rem 1.5rem;
      width: 16.5rem;
      height: 7.4rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
      border-radius: 0.6rem;
      display: flex;
      position: relative;
      .pic17 {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 6.55rem;
        height: 3.55rem;
      }

      .left {
        width: 4rem;
        height: 100%;
        background: #fff;
        margin-right: 0.6rem;
        border-radius: 0.5rem;
      }
      .right {
        position: relative;
        z-index: 9;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .text {
          font-size: 1.6rem;
          color: #fff;
          font-weight: bold;
        }
        .tips {
          color: #fff;
          font-size: 1.2rem;
        }
      }
    }
  }
}

.indexMainContent {
  padding: 2rem 1.5rem;
  position: relative;
  margin-top: -1.5rem;
  background: #fffbf1;
  border-radius: 1.5rem 1.5rem 0 0;
  /deep/ .van-search {
    flex: 1;
    padding: 0;
    border-radius: 999px;
    .van-search__content--round {
      border: 1px solid #d12c21;
    }

    .van-field__control {
      color: #666;
    }
    .van-field__control::placeholder {
      color: #666;
    }

    .van-icon-search {
      color: #666;
    }
  }
  .searchBtn {
    border: 0;
    margin-left: 2rem;
    padding: 0 2rem;
    height: 3.6rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    .van-button__text {
      color: #fff;
    }
  }
  .kong {
    text-align: center;
    margin: 2rem 0;
  }
  .productsList {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1.4rem;
    max-height: 78vh;
    overflow: scroll;
    .productsItem {
      .img {
        border-radius: 0.5rem 0.5rem 0 0;
        width: 16.45rem;
        height: 16.45rem;
        display: block;
      }

      .priceBox {
        height: 9.3rem;
        background: #fff;
        border-radius: 0 0 2rem 2rem;
        padding: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }

      .info {
        font-size: 1.2rem;
        line-height: 1.8rem;
        font-weight: bold;
      }
      .price {
        font-weight: bold;
        font-size: 1.8rem;
        color: #d12c21;
      }
    }
  }
}

.applyingMerchantsPopup {
  border-radius: 0.5rem;
  width: 90vw;
  background: #fcefd3;

  .content {
    position: relative;
    padding: 2rem;
  }
  .close {
    position: absolute;
    top: 1rem;
    right: 2rem;
    width: 3rem;
    height: 3rem;
  }
  .title {
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
  }

  .merchantName {
    margin: 2rem 0;
    width: 100%;
    height: 4rem;
    border: 0;
    padding-left: 1.5rem;
    box-sizing: border-box;
    border-radius: 0.5rem;
  }
  .pic6 {
    position: absolute;
    right: 0;
    bottom: 0.2rem;
    width: 5rem;
    height: 3rem;
  }

  .pic7 {
    position: absolute;
    top: 2rem;
    left: 0;
    width: 6rem;
    height: 2.5rem;
  }
  .confirm {
    height: 5rem;
    background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
    box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
    border-radius: 0.8rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: bold;
  }
}
</style>



